import React, {Component} from 'react';
import PropTypes from 'prop-types';
import { Beforeunload } from 'react-beforeunload';
/**
 * window_close is an example component.
 * Component Will emit the event when window is closed.
 */
export default class window_close extends Component {
    render() {
        const {id,  setProps, value} = this.props;

        return (
            <div id={id}>
               <Beforeunload onBeforeunload={(event) => {event.preventDefault(),
                setProps({ value: 'close' })   } } />
            </div>
        );
    }
}

window_close.defaultProps = {};

window_close.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string.isRequired,
    /**
     * The value displayed in the input.
     */
    value: PropTypes.string,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};
